import React, { useState } from 'react';
import Cards from '../UI/Cards';
import classes from './AddUser.module.css';
import Button from '../UI/Button';
import ErrorModal from '../UI/ErrorModal';

const AddUser= (props)=> {

    const [enteredUserName, setEnteredUserName] = useState('');
    const [enteredAge, setEnteredAge]= useState('');
    const [error, setError]= useState();

    const addUserHandler = (event)=> {
        event.preventDefault();

        if(enteredUserName.trim().length === 0 || enteredAge.trim().length === 0) 
        {
            setError({
                title: "please enter valid input",
                message: "Please enter a name and positive age (> 0)"
            })
            return;
        }
        if(+enteredAge < 1) 
        {
            setError({
                title: "Invalid age",
                message: "Please enter a positive age ( > 0)"
            })
            return;  // here we are converting the data type of enteredAge from string to number
        }
        props.onAddUser(enteredUserName,enteredAge);
        setEnteredUserName('');
        setEnteredAge('');
    }

    const nameChangeHandler = (event)=> {
        setEnteredUserName(event.target.value);
    }
    const ageChangeHandler = (event)=> {
        setEnteredAge(event.target.value);
    }

    const errorHandler = ()=>{
        setError(null);
    }

        return (
            <div>
                {error && <ErrorModal title={error.title} message={error.message} onConfirm={errorHandler}/>}
                <Cards className={classes.input}>
                <form onSubmit={addUserHandler}>
                    <lebel htmlFor="username">User name:</lebel>
                    <input id="username" type="text" value={enteredUserName} onChange={nameChangeHandler} />
                    <lebel htmlFor="age">Age (Years)</lebel>
                    <input id="age" type="number" value={enteredAge} onChange={ageChangeHandler}/>
                    <Button type="submit" >Add user</Button>
                 </form>
            </Cards>
            </div>
            
        );
}

export default AddUser;